@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500&display=swap);
:root {
  --outer-space-crayola: hsla(194, 33%, 18%, 1);
  --teal-blue: hsla(195, 37%, 40%, 1);
  --green-sheen: hsla(172, 25%, 55%, 1);
  /* --almond-dark: hsla(29, 97%, 86%, 1);
  --almond-medium: hsla(28, 56%, 87%, 1); */
  --almond-light: hsla(25, 100%, 95%, 1);
  --cambridge-blue: hsla(129, 22%, 69%, 1); /* not-used */
  --tea-green: hsla(95, 32%, 82%, 1);
  --light-grey-1: hsla(0, 9%, 98%, 1);
  --light-grey-2: hsla(0, 8%, 95%, 1);
  --dark-grey-1: 	hsla(220, 13%, 18%, 1);
  
  /* SHEER COLORS FOR FILTER EFFECTS */
  --outer-space-crayola-sheer: hsla(194, 33%, 18%, .6);
  --green-sheen-sheer: hsla(172, 25%, 55%, .6);
  --teal-blue-sheer: hsla(195, 37%, 40%, .6);
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  line-height: 1.4;
  color: hsla(0, 9%, 98%, 1);
  color: var(--light-grey-1);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
  1500px = 93.75em;
  1200px = 75em; 
  900px = 56.25em;
  700px = 43.75em;
  600px = 37.5em; 
  400px = 25em; 
*/
@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}

.Header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;color:white;background-image:linear-gradient(to right bottom, var(--outer-space-crayola-sheer), var(--teal-blue-sheer)),url("https://images.unsplash.com/photo-1484417894907-623942c8ee29?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=3289&q=80");background-repeat:no-repeat;background-position:center;background-size:cover;position:relative}.Header__welcome-message{font-family:'Roboto Slab', sans-serif;font-size:4rem;position:absolute;top:30%;left:10%;width:40%;font-weight:500;letter-spacing:1px}@media only screen and (max-width: 75em){.Header__welcome-message{font-size:3rem;width:50%}}@media only screen and (max-width: 37.5em){.Header__welcome-message{width:60%}}

.NavigationItem{position:relative;padding-bottom:1rem}.NavigationItem__link,.NavigationItem__link:link,.NavigationItem__link:visited{color:var(--light-grey-1);border:none;cursor:pointer;text-decoration:none;height:100%;width:100%;display:flex;justify-content:center;align-items:center}.NavigationItem.selected{border-bottom:2px solid var(--outer-space-crayola)}.NavigationItem:not(:last-of-type){margin-right:4rem}.NavigationItem:not(.selected)::after{content:'';position:absolute;bottom:0;left:50%;width:0;border:1px solid transparent;transition:all .2s}.NavigationItem:not(.selected):hover::after{content:'';position:absolute;width:100%;left:0;border-color:var(--outer-space-crayola)}@media only screen and (max-width: 75em){.NavigationItem{padding-bottom:0.5rem}.NavigationItem:not(:last-of-type){margin-right:2.5rem}}@media only screen and (max-width: 56.25em){.NavigationItem{height:20%;width:100%;display:flex;justify-content:center;align-items:center;font-size:3rem;background-color:var(--teal-blue);transition:transform .5s;transform:translateX(-100%);opacity:0}.NavigationItem.selected,.NavigationItem:not(.selected)::after{border:none}.NavigationItem:nth-child(1){transition-delay:100ms}.NavigationItem:nth-child(2){transition-delay:200ms}.NavigationItem:nth-child(3){transition-delay:300ms}.NavigationItem:nth-child(4){transition-delay:400ms}.NavigationItem:nth-child(5){transition-delay:500ms}.NavigationItem.open{transform:translateX(0);opacity:1}.NavigationItem:nth-last-of-type(even){background-color:var(--green-sheen)}}

.Navigation{position:fixed;top:0;left:0;min-width:100%;display:flex;padding:3rem 5rem 2rem 5rem;text-transform:uppercase;font-size:1.6rem;font-weight:600;letter-spacing:2px;background-color:var(--teal-blue);color:var(--light-grey-2);cursor:pointer;z-index:10}.Navigation__checkbox{display:none}.Navigation__brand{text-transform:uppercase;margin-right:auto}.Navigation__list{display:flex;list-style:none}@media only screen and (max-width: 56.25em){.Navigation__checkbox:checked .NavigationItem{transform:translateX(0%)}.Navigation__checkbox:checked+.Navigation__button .Navigation__icon{background-color:transparent}.Navigation__checkbox:checked+.Navigation__button .Navigation__icon::before{top:0;transform:rotate(135deg)}.Navigation__checkbox:checked+.Navigation__button .Navigation__icon::after{top:0;transform:rotate(-135deg)}.Navigation__button{background-color:var(--light-grey-2);height:5rem;width:5rem;border-radius:50%;position:fixed;top:1rem;right:2rem;z-index:3000;box-shadow:0 1rem 1rem rgba(0,0,0,0.22);text-align:center;cursor:pointer}.Navigation__button:hover .Navigation__icon::before{top:-1rem}.Navigation__button:hover .Navigation__icon::after{top:1rem}.Navigation__icon{position:relative;margin-top:50%}.Navigation__icon,.Navigation__icon::before,.Navigation__icon::after{width:2.5rem;height:2px;background-color:var(--dark-grey-1);display:inline-block;transition:transform .2s}.Navigation__icon::before,.Navigation__icon::after{content:"";position:absolute;left:0}.Navigation__icon::before{top:-0.8rem}.Navigation__icon::after{top:0.8rem}.Navigation__list{position:absolute;display:flex;flex-direction:column;top:0;left:0;height:100vh;width:100vw}}

.AboutMe{min-height:100vh;padding:10rem 15rem;display:flex;flex-direction:column;background-color:var(--outer-space-crayola)}.AboutMe__container{display:flex;align-items:center}.AboutMe__image{height:35rem;background-size:auto;border-radius:50%;box-shadow:0 2rem 2rem rgba(0,0,0,0.3)}.AboutMe__text-outer-container{padding:5rem 10rem;margin-left:5rem;background-color:var(--green-sheen);transform:skewX(-10deg);font-size:1.7rem;box-shadow:0 2rem 2rem rgba(0,0,0,0.3)}.AboutMe__text-outer-container>*{transform:skewX(10deg)}.AboutMe__text{line-height:1.8}.AboutMe__text:not(:last-of-type){margin-bottom:2.5rem}@media only screen and (max-width: 93.75em){.AboutMe__text-outer-container{font-size:1.6rem}}@media only screen and (max-width: 75em){.AboutMe{padding:7.5rem 7.5rem}.AboutMe__container{justify-content:space-around}.AboutMe__image{height:28rem;margin-right:2rem}.AboutMe__text-outer-container{transform:skewX(0);padding:3rem 5rem;margin:0;width:80rem;max-width:100%}.AboutMe__text-outer-container>*{transform:skewX(0)}}@media only screen and (max-width: 56.25em){.AboutMe__image{margin-bottom:6rem}.AboutMe__container{flex-direction:column;justify-content:space-between}}@media only screen and (max-width: 37.5em){.AboutMe{padding:4rem 2rem}.AboutMe__text-outer-container{padding:1rem 2rem}}

.SectionHeader{font-family:'Roboto Slab', sans-serif;text-transform:uppercase;font-weight:600;font-size:5rem;margin:1.5rem 0 3rem 0}.SectionHeader span{display:block}@media only screen and (max-width: 56.25em){.SectionHeader{font-size:4.5rem;text-align:center}.SectionHeader span{display:inline}}

.Skills{min-height:100vh;padding:10rem 15rem;display:flex;flex-direction:column;justify-content:center;background-color:var(--green-sheen)}.Skills__section-heading{font-family:'Roboto Slab', sans-serif;text-transform:uppercase;font-weight:600;font-size:4rem;margin-bottom:5rem}.Skills__section-heading span{display:block}.Skills__container{display:flex;flex-wrap:wrap}@media only screen and (max-width: 75em){.Skills{padding:7.5rem 7.5rem}.Skills__container{flex-direction:column}}@media only screen and (max-width: 37.5em){.Skills{padding:4rem 2rem}}

.SkillBoxItem{display:flex;align-items:center;font-size:1.6rem}.SkillBoxItem:not(:last-of-type){margin-bottom:3.5rem}.SkillBoxItem__icon{width:4rem;margin-right:3rem}

.SkillBox{width:40%;background-color:var(--almond-light);color:var(--dark-grey-1);font-weight:600;box-shadow:0 2rem 4rem rgba(0,0,0,0.3);border-radius:5px;overflow:hidden;margin:0 auto 5rem auto}.SkillBox__heading{font-family:'Roboto Slab', sans-serif;background-color:var(--outer-space-crayola);color:var(--light-grey-1);text-transform:uppercase;text-align:center;padding:2.5rem 0;box-shadow:0 1rem 2rem rgba(0,0,0,0.3);font-size:1.8rem}.SkillBox__list{list-style:none;padding:4rem}@media only screen and (max-width: 93.75em){.SkillBox{width:40%}}@media only screen and (max-width: 75em){.SkillBox{width:80%}}@media only screen and (max-width: 43.75em){.SkillBox{width:95%}}

.Projects{min-height:100vh;padding:10rem 15rem;background-color:var(--outer-space-crayola);display:flex;flex-direction:column;font-size:1.8rem}.Projects__text-container{margin:0 0 5rem 0}.Projects__text:not(:last-of-type){margin-bottom:2rem}.Projects__container{display:flex;flex-wrap:wrap}.Projects__box{width:30%;background-color:var(--almond-light);color:var(--dark-grey-1);font-weight:600;box-shadow:0 2rem 4rem rgba(0,0,0,0.3);border-radius:5px;overflow:hidden;margin:0 auto 5rem auto}.Projects__heading{font-family:'Roboto Slab', sans-serif;background-color:var(--green-sheen);color:var(--light-grey-1);text-transform:uppercase;text-align:center;padding:2.5rem 0;box-shadow:0 1rem 2rem rgba(0,0,0,0.3);font-size:1.8rem}.Projects__detail-container{padding:3rem}.Projects__detail{font-size:1.5rem}.Projects__detail:not(:last-of-type){margin-bottom:2rem}@media only screen and (max-width: 75em){.Projects{padding:7.5rem 7.5rem;font-size:1.6rem}.Projects__container{flex-direction:column}}@media only screen and (max-width: 37.5em){.Projects{padding:4rem 2rem}}

.ProjectBox{width:40%;background-color:var(--almond-light);color:var(--dark-grey-1);font-weight:600;box-shadow:0 2rem 4rem rgba(0,0,0,0.3);border-radius:5px;overflow:hidden;margin:0 auto 5rem auto}.ProjectBox__heading{font-family:'Roboto Slab', sans-serif;background-color:var(--green-sheen);color:var(--light-grey-1);text-transform:uppercase;text-align:center;padding:2.5rem 0;box-shadow:0 1rem 2rem rgba(0,0,0,0.3);font-size:1.8rem}.ProjectBox__detail-container{padding:3rem}.ProjectBox__detail{font-size:1.5rem}.ProjectBox__detail:not(:last-of-type){margin-bottom:2rem}@media only screen and (max-width: 75em){.ProjectBox{width:60%}}@media only screen and (max-width: 37.5em){.ProjectBox{width:95%}}

.Contact{min-height:100vh;padding:10rem 15rem;background-color:var(--green-sheen)}.Contact__text-container{margin:0 0 5rem 0}.Contact__text{font-size:1.8rem}.Contact__text:not(:last-of-type){margin-bottom:2rem}.button,.button:link,.button:visited{padding:1.5rem 3rem;color:white;text-decoration:none;font-size:1.5rem;text-align:center;background-color:var(--outer-space-crayola);display:inline-block;margin-top:3rem;border-radius:5px;box-shadow:0 1rem 2rem rgba(0,0,0,0.3);transition:all .2s}.button:hover,.button:link:hover,.button:visited:hover{transform:translateY(-3px)}.button:active,.button:link:active,.button:visited:active{transform:translateY(0);box-shadow:0 0.5rem 1rem rgba(0,0,0,0.5)}@media only screen and (max-width: 75em){.Contact{padding:7.5rem 7.5rem}}@media only screen and (max-width: 56.25em){.Contact{text-align:center}}@media only screen and (max-width: 37.5em){.Contact{padding:5rem 2rem}}

.App{width:100%;min-width:100%}

